import React from 'react';
import { Layout, Menu, Dropdown, Button, Icon } from 'antd';
import { connect } from 'react-redux';
import { injectIntl } from 'react-intl';
import { bindActionCreators } from 'redux';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

import { changeLang } from './../../reducers/auth';
import { config } from './../../config';

import './index.css';

const { Header } = Layout;
const { SubMenu } = Menu;

const Logo = styled.div`
  overflow: hidden;
  padding-left: 40px;
  float: left;
  height: 64px;
  line-height: 64px;
  text-decoration: none;
  white-space: nowrap;
  img {
    height: 50px;
    margin-right: 16px;
  }
`;

const HeaderCmp = props => {
  const {
    intl: { messages },
  } = props;

  const menu = (
    <Menu>
      <Menu.Item
        key="en"
        disabled={props.lang === 'en'}
        onClick={() => {
          props.authChangeLang('en');
        }}
      >
        {/* <span role="img" aria-label="English">
          🇺🇸
        </span> */}
        {messages.languages.en}
      </Menu.Item>
      <Menu.Item
        key="es"
        disabled={props.lang === 'es'}
        onClick={() => {
          props.authChangeLang('es');
        }}
      >
        {/* <span role="img" aria-label="Spanish">
          🇪🇸
        </span> */}
        {messages.languages.es}
      </Menu.Item>
    </Menu>
  );

  const hideMenu = (
    <Menu>
      <SubMenu title="Language">{menu}</SubMenu>
      <Menu.Item>
        <Link className="list-inline-item visible-sm " to="/main/signup">
          <Icon type="enter" />
          &nbsp; {messages.default.signUp}
        </Link>
      </Menu.Item>
      <Menu.Item>
        <Link className="list-inline-item visible-sm " to="/main/signin">
          <Icon type="login" />
          &nbsp; {messages.default.signIn}
        </Link>
      </Menu.Item>
    </Menu>
  );
  return (
    <Header className="app-header ">
      <div className="app-header-inner change-header">
        <div className="header-left">
          <div className="list-unstyled list-inline">
            <Link to="/main" className="list-inline-item">
              <Logo>
                <img alt="logo" src={config[config.default].logo} />
              </Logo>
            </Link>
          </div>
        </div>

        <div className="header-right">
          <div className="list-unstyled list-inline">
            <Dropdown
              overlay={hideMenu}
              className="list-inline-item "
              trigger={['click']}
              placement="bottomRight"
            >
              <Link to="#" className="list-inline-item hidden-sm">
                <Button type="primary" shape="circle" icon="arrow-down"></Button>
              </Link>
            </Dropdown>
            <Dropdown
              overlay={menu}
              className="list-inline-item "
              trigger={['click']}
              placement="bottomRight"
            >
              <Link to="#" className="list-inline-item visible-sm">
                <Button type="primary" shape="circle" icon="global"></Button>
                {/* <Icon type="global" className="list-notification-icon" /> */}
              </Link>
            </Dropdown>
            <Link className="list-inline-item visible-sm " to="/main/signup">
              <Button type="primary">{messages.default.signUp}</Button>
            </Link>
            <Link className="list-inline-item visible-sm " to="/main/signin">
              <Button type="primary">{messages.default.signIn}</Button>
            </Link>
          </div>
        </div>
      </div>
    </Header>
  );
};

const mapStateToProps = state => {
  return {
    lang: state.rootReducer.auth.lang,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    authChangeLang: bindActionCreators(changeLang, dispatch),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(injectIntl(HeaderCmp));
