import React, { Component } from 'react';
import { Switch, Route, Redirect, withRouter } from 'react-router-dom';
import loadable from 'react-loadable';
import LoadingComponent from 'components/AppComponents/Loading';

import AppLayout from 'components/AppLayout';
import ContentCmp from 'components/Content';
import PayCancel from 'components/PayCancel';
import PayError from 'components/PayError';
import PaySuccess from 'components/PaySuccess';
import { HeadCmp } from 'components/HeadCmp';

import auth from './../../utils/auth';

import './index.css';

class Main extends Component {
  componentWillMount = () => {
    const token = auth.getToken();
    if (token) {
      this.props.history.push('/dashboard');
    }
  };

  render() {
    let AsyncProcess = loadable({
      loader: () => import('containers/Process'),
      loading: LoadingComponent,
    });
    let AsyncLanding = loadable({
      loader: () => import('containers/Landing'),
      loading: LoadingComponent,
    });
    let AsyncSignin = loadable({
      loader: () => import('containers/Auth/Signin'),
      loading: LoadingComponent,
    });
    let AsyncSignup = loadable({
      loader: () => import('containers/Auth/Signup'),
      loading: LoadingComponent,
    });
    let AsyncDownload = loadable({
      loader: () => import('components/Download'),
      loading: LoadingComponent,
    });
    let AsyncPayProcess = loadable({
      loader: () => import('components/PayProcess'),
      loading: LoadingComponent,
    });

    const Parent = ({ children }) => {
      return <ContentCmp>{children}</ContentCmp>;
    };

    return (
      <AppLayout>
        <HeadCmp />
        <Switch>
          <Route exact path="/main" component={AsyncLanding} />
          <Route exact path="/main/process" component={AsyncProcess} />
          <Route exact path="/main/process/pay" component={AsyncPayProcess} />
          <Route exact path="/main/download/:uuid" component={AsyncDownload} />
          <Route
            exact
            path="/main/process/:uuid/success"
            render={() => (
              <Parent>
                <PaySuccess />
              </Parent>
            )}
          />
          <Route
            exact
            path="/main/process/:uuid/cancel"
            render={() => (
              <Parent>
                <PayCancel />
              </Parent>
            )}
          />
          <Route
            exact
            path="/main/process/:uuid/error"
            render={() => (
              <Parent>
                <PayError />
              </Parent>
            )}
          />
          <Route exact path="/main/signin" component={AsyncSignin} />
          <Route exact path="/main/signup" component={AsyncSignup} />
          <Route render={() => <Redirect to="/main/404" />} />
        </Switch>
      </AppLayout>
    );
  }
}

export default withRouter(Main);
