const functions = {
  hasErrors: fieldsError => {
    return Object.keys(fieldsError).some(field => fieldsError[field]);
  },
  getBase64: file => {
    return new Promise((resolve, reject) => {
      let reader = new FileReader();
      reader.readAsDataURL(file);
      let tmp = {
        fileName: file.name,
        fileType: file.type,
      };
      reader.onload = function() {
        tmp['file'] = reader.result;
        resolve(tmp);
      };

      reader.onerror = function(error) {
        reject(error);
      };
    });
  },
  checkFile: (file, allowedFiles) => {
    var extension = file.name.substr(file.name.lastIndexOf('.') + 1);
    var regEx = new RegExp(allowedFiles, 'gi');
    if (!regEx.test(extension)) {
      return false;
    }
    return true;
  },
  getStatus: status => {
    status = parseInt(status);

    if (status < 0) {
      return 'Processing Error';
    }
    if (status === 3) {
      return 'Waiting';
    }
    if (status === 5) {
      return 'Waiting Quoting';
    }
    if (status === 6) {
      return 'Quoting';
    }
    if (status === 7) {
      return 'Quotes Ready';
    }
    if (status === 17) {
      return 'Pending Accept/Reject';
    }
    if (status === 20) {
      return 'Preprocessing';
    }
    if (status === 30) {
      return 'Processing';
    }
    if (status === 40) {
      return 'PostProcessing';
    }
    if (status === 100) {
      return 'Finished';
    }
    if (status === 110 || status === 120) {
      return 'Downloaded';
    }
    return 'Waiting';
  },
  languages: {
    'en-gb': 'English (UK)',
    'en-GB': 'English (UK)',
    'en-us': 'English (USA)',
    'en-US': 'English (USA)',
    'pt-br': 'Portuguese Brazil',
    'pt-pt': 'Portuguese Portugal',
    'zh-cn': 'Simplified Chinese',
    'zh-tw': 'Traditional Chinese',
    am: 'Amharic',
    ar: 'Arabic',
    auto: 'Auto',
    bg: 'Bulgarian',
    bn: 'Bengali',
    ca: 'Catalan',
    chr: 'Cherokee',
    cs: 'Czech',
    cy: 'Welsh',
    da: 'Danish',
    de: 'German',
    el: 'Greek',
    en: 'English',
    es: 'Spanish',
    et: 'Estonian',
    eu: 'Basque',
    fa: 'Persian',
    fi: 'Finnish',
    fil: 'Filipino',
    fr: 'French',
    gu: 'Gujarati',
    he: 'Hebrew',
    hi: 'Hindi',
    hr: 'Croatian',
    hu: 'Hungarian',
    id: 'Indonesian',
    is: 'Icelandic',
    it: 'Italian',
    iw: 'Hebrew',
    ja: 'Japanese',
    kn: 'Kannada',
    ko: 'Korean',
    lt: 'Lithuanian',
    lv: 'Latvian',
    ml: 'Malayalam',
    mr: 'Marathi',
    ms: 'Malay',
    nl: 'Dutch',
    no: 'Norwegian',
    pl: 'Polish',
    pp: 'Portuguese Portugal',
    pt: 'Portuguese Brazil',
    ro: 'Romanian',
    ru: 'Russian',
    sk: 'Slovak',
    sl: 'Slovenian',
    sr: 'Serbian',
    sv: 'Swedish',
    sw: 'Swahili',
    ta: 'Tamil',
    te: 'Telugu',
    th: 'Thai',
    tr: 'Turkish',
    uk: 'Ukrainian',
    ur: 'Urdu',
    va: 'Valencian',
    vi: 'Vietnamese',
    zh: 'Simplified Chinese',
  },

  getStatusByText: status => {
    const aux = {
      processing_error: 'Processing Error',
      waiting: 'Waiting',
      waiting_quoting: 'Waiting Quoting',
      quoting: 'Quoting',
      quote_ready: 'Quotes Ready',
      pending_accept_reject: 'Pending Accept/Reject',
      preprocessing: 'Pre processing',
      processing: 'Processing',
      post_processing: 'Post Processing',
      finished: 'Finished',
      downloaded: 'Downloaded',
    };
    return aux[status];
  },
};

export default functions;
